.table{width:100%;table-layout:fixed}.table thead th.sortable{cursor:pointer;user-select:none}.table thead th .sortArrow{display:inline-block;border:5px solid transparent;border-top-color:var(--ps-ui-foreground-text);top:3px;position:relative;margin-left:7px}.table thead th .sortArrow.asc{border-top-color:transparent;border-bottom-color:var(--ps-button-switch-bg-highlight);top:-2px}.table thead th .sortArrow.desc{border-top-color:var(--ps-button-switch-bg-highlight);top:3px}.table thead,.table tbody tr:nth-child(2n){background-color:var(--ps-neutral-9)}.table tbody{cursor:pointer}.table tbody td{font-weight:initial;text-align:center}.table tbody tr.isRowSelected{cursor:pointer;background:var(--ps-table-highlight-row-bg) !important;color:var(--ps-table-highlight-row-text)}.table tbody tr.isRowDisabled td{text-decoration:line-through}.table tbody tr:not(.isRowSelected):hover{cursor:pointer;color:var(--ps-neutral-2);background:var(--ps-ui-element-bg-highlight)}.table tbody td,.table tbody th{border:1px solid var(--ps-ui-border);padding:4px 10px;width:10%}.loadingSpinner{text-align:center;margin-top:50px}.pagination{display:flex;justify-content:flex-end}