@import'@szhsin/react-menu/dist/index.css';.dropdownMenu{padding:.25rem 0;box-shadow:0px 2px 6px var(--ps-dropdown-shadow) !important;background-color:var(--ps-dropdown-background) !important;color:var(--ps-neutral-2) !important}.dropdownMenu ul[class*=szh-menu]{color:var(--ps-neutral-2) !important;background-color:var(--ps-dropdown-background) !important}.dropdownMenu li[class=szh-menu__header]{text-align:left}.dropdownMenu li[class*=hover],.dropdownMenu div[class*=hover]{background-color:var(--ps-ui-element-bg-highlight)}.dropdownMenu [class*=szh-menu--open]:empty{display:none}.dropdownMenu [class*="szh-menu__item active"]{background-color:var(--ps-ui-element-bg-highlight);color:var(--ps-selected-app)}.dropdownMenu [class*=szh-menu__item--focusable]{background-color:inherit !important;padding-top:1rem;padding-bottom:1rem}.dropdownMenu [class*=szh-menu__item--focusable] input{width:100%}.dropdownMenuButton{outline:none;display:flex;flex-shrink:0;position:relative;border-radius:4px;background-color:var(--ps-ui-element-bg-primary);border:1px solid var(--ps-ui-border);margin-right:5px;padding-right:20px}.dropdownMenuButton::after{content:"▾";position:absolute;top:3px;right:5px}.dropdownMenuButton:hover{cursor:pointer;background-color:var(--ps-ui-element-bg-highlight)}