.tooltip,.left,.right,.bottom,.top,.visible{position:absolute;white-space:nowrap;border:1px solid var(--ps-ui-border);display:none;z-index:2;background-color:var(--ps-ui-border);border-radius:4px;font-size:14px;line-height:16px;padding:4px 8px;cursor:default}.tooltip::before,.left::before,.right::before,.bottom::before,.top::before,.visible::before{content:"";width:0;height:0;margin:auto;position:absolute;border:6px solid transparent}.visible{display:block}.top::before{border-top:6px solid var(--ps-ui-border);bottom:-13px;left:0;right:0}.bottom::before{border-bottom:6px solid var(--ps-ui-border);top:-13px;left:0;right:0}.right::before{border-right:6px solid var(--ps-ui-border);left:-13px;top:0;bottom:0}.left::before{border-left:6px solid var(--ps-ui-border);right:-13px;top:0;bottom:0}.top{bottom:calc(100% + 7px)}.bottom{top:calc(100% + 7px)}.right{left:calc(100% + 7px)}.left{right:calc(100% + 7px)}.muiTooltip.muiTooltip{border:1px solid var(--ps-mui-tooltip-background);background-color:var(--ps-mui-tooltip-background);border-radius:4px;font-size:14px;line-height:16px;padding:4px 8px}.muiTooltipArrow.muiTooltipArrow{color:var(--ps-mui-tooltip-background)}