.box,.collapseBox{background-color:var(--ps-ui-foreground);border:1px solid var(--ps-ui-border);border-radius:4px;position:relative}.padding{margin-bottom:10px;padding:10px}.collapseBox{margin-bottom:10px}.collapseBox .box,.collapseBox .collapseBox{background-color:transparent;border:none;border-radius:0;padding-bottom:0;height:auto}.collapseBox .box.collapsedContent,.collapseBox .collapsedContent.collapseBox{height:0;margin:0;padding:0;overflow:hidden}.collapseTitle{font-weight:500;font-size:18px;cursor:pointer;padding:5px 10px;display:flex;justify-content:space-between;align-items:center;height:40px}.collapseTitle:hover{background-color:var(--ps-ui-border)}.collapseIcon{display:flex;transition:transform .1s}.collapseIcon.collapsed{transform:rotate(90deg);display:flex}